<template>
    <div >
        <!--Row>
            <search-form :tips="'请输入机种名称'"></search-form>
        </Row-->
        <div v-if="selected_model === ''">
            <Row>
                <Col span="12" offset="6">
                    <select-form :tips="'请选择或输入机种名称'" :selectOption="selected_model"
                                 @select-changed="selectChanged"
                                 
                                 :optionList="optionList"></select-form>
                </Col>
            </Row>
        </div>
        <div v-else >
            <Row>
                <Col span="12" offset="6">
                    <select-form :tips="'请选择或输入机种名称'" :selectOption="selected_model"
                                 @select-changed="selectChanged"
                                 :optionList="optionList"></select-form>
                </Col>
            </Row>
            <Row @change_button_status="change_addbutton_status" v-model="add_model_button">
                <model-table v-model="tableData" 
                            :columnsList="tableColumns" 
                            :isLoading="tableLoading"
                            @on-change="row_change" 
                            @on-delete="row_delete" 
                            style="margin-top: 10px"
                            
                            >
                </model-table>
            </Row>
            <!--<Row>
                <page-table></page-table>
            </Row>-->
            <Row v-if="add_model_button != ''" style ="margin-top:5%">
                <Col span="8" offset="7">
                    <model-form
                            ref="formRef"
                            v-model="modelFormData"
                            :formItems="newModelForm"
                            :formDataVaildate="formDataVaildate"
                            @on-click-submit="formSubmit">
                    </model-form>
                </Col>
            </Row>
        </div>
    </div>
</template>

<script>
    import modelTable from '../common/table/table-list.vue';
    import searchForm from '../common/search/input-search.vue';
    import selectForm from '../common/form/select-form.vue';
    import pageTable  from '../common/pagination/page-table.vue';
    import modelForm from '../common/form/best-form.vue';
    import newModelForm from './create_model.js';
    import util  from '../../libs/util.js';
    //import parser from '../../libs/parser.js';
    import viewData from './data.js';

    import devmanage_th from './dev_manage_th.js';

    export default{
        components: {
            modelTable,
            searchForm,
            selectForm,
            pageTable,
            modelForm
        },
        data() {
            const validateFileinput = (rule, value, callback) => {
                
                if(value === ''){
                    callback(new Error('Please upload template file!'));
                }else if(value != ''){
                    callback();
                }
            };
            const validateModelname = (rule, value, callback) => {
                if(value === ''){
                    callback(new Error('Please enter model name!'));
                }else if(value != ''){
                    for(var i=0;i<this.optionList.length;i++){
                        if(value === this.optionList[i].value){
                            callback(new Error('This model name is exist!'));
                        }
                    }
                    callback();
                }
             };
            return {
                tableData:[],
                tableDatas:[],
                tableColumns: [],
                tableLoading: false,
                totalsize: 0,
                page_size: 10,

                newModelForm: newModelForm,
                modelFormData: {},
                selected_model: '',
                add_model_button: '',
                show: false,
                formDataVaildate:{ 
                    model_name:[
                        { required: true, validator:validateModelname, trigger: 'change' }
                    ],
                    basic_platform: [
                        { required: true, message: 'Please enter basic platform name!', trigger: 'blur' }
                    ],
                    brand: [
                        { required: true, message: 'Please enter brand name!', trigger: 'blur' }
                    ],
                    // template: [
                    //     { required: true, validator:validateFileinput, trigger: 'blur' }
                    // ]
                },
                optionList:[],
                baseuri:'/devmanagements/'
            };
        },
        methods: {
            //初始表单数据
            initFormData: function () {
                newModelForm.forEach((item, index) => {
                    this.$set(this.modelFormData, item.key, newModelForm[index].value);
                });
            },
            //初始表单校验规则
            initFormDataVaildate: function () {
                newModelForm.forEach((item) => {
                    this.modelFormData[item.key] = '';
                });
            },
            //表单提交
            formSubmit: function (vaild, formDataObject) {
//                util.ajax.request('post', this.baseuri, {}, this.formDataObject, (res) => {
//                    this.$Message.success('Are you ok!');
//                });
                let formData = new FormData();
                Object.keys(formDataObject).forEach((key) => {
                    formData.append(key, formDataObject[key]);
                });
                //console.log(formDataObject.uploadFileName);
                //util.test.post('posts', formData);
                let forddata = JSON.parse(JSON.stringify(formDataObject));
                console.log(forddata);
                util.ajax.request('post', 'http://192.168.70.167/model-test/add_newmodel', {},forddata ,(res) => {console.log('hello world')},this.exception_callback);
                if (vaild)
                    this.$Message.success('Success !');
                else
                    this.$Message.error('Failed !');
            },
            //筛选条件发生变化
            selectChanged: function (event) {
                this.selected_model = event;
                this.get_tabledata(this.selected_model);
            },
            //点击 + 出现添加机种的页面
            change_addbutton_status: function(msg) {
                if(this.add_model_button){
                    this.add_model_button = '';
                }else{
                    this.add_model_button = msg;
                }
                
                //console.log('hello world');
            },
            //获取tabledata
            get_tabledata(val){
                for(var i = 0;i < this.tableDatas.length;i++){
                    if(this.tableDatas[i].model_name == val){
                        var devinfo = new Array(1);
                        devinfo[0] = this.tableDatas[i];
                        this.tableData = devinfo;
                    }
                }
            },
            //table内容更新
            row_change (row_data, index) {
                let uri = this.baseuri + row_data[index].model_name;
                this.tableLoading = true;
                let params = {
                    limit: this.page_size
                };
                util.ajax.request('patch', uri, params, row_data[index], (res) => {this.tableLoading = false}, this.exception_callback);
            },
            //table数据被删除
            row_delete (del_data) {
                //console.log(del_data);
                let uri = this.baseuri ;
                this.tableLoading = true;
                let params = {
                    limit: this.page_size
                };
                util.ajax.request('delete', uri, params, {}, (res) => {this.tableLoading = false}, this.exception_callback);
            },
            //ajax请求返回值处理
            response_callback(res) {
                this.tableLoading = false;
                //this.tableData = res.data.body;
                this.tableDatas = res.data.body;
                
                this.totalsize = res.data.count;
                this.get_model_name_list(res.data.body);
            },
            //ajax请求错误异常处理
            exception_callback(err) {
                console.log(err);
            },
            //获取model_name_list
            get_model_name_list(val){
                //console.log(JSON.stringify(val));
                var model_name_list = new Array(val.length);
                for (var i = 0; i < val.length; i++) {
                    model_name_list[i] = {};
                    model_name_list[i].label = val[i].model_name;
                    model_name_list[i].value = val[i].model_name;
                }
                this.optionList = model_name_list;
            }

        },
        created: function (){
            this.newModelForm = newModelForm;
            this.initFormData();

            let params = {
                limit: this.page_size
            };
            //this.tableData =
            this.tableColumns = devmanage_th.dev_manage_list_col_th;
            this.add_model_button = devmanage_th.this_test;
            console.log("11111111");
            console.log(devmanage_th.this_test);

            this.tableLoading = true;
            util.ajax.request('get', this.baseuri, params, {}, this.response_callback, this.exception_callback);
            //这里的ajax请求后台的读出所有机种的信息
            //parser.viewJson(viewData);
            //this.initFormDataVaildate();
            // this.optionList=[
            //         {
            //             label: 'ZN-7100-2DHO',
            //             value: '1022'
            //         },
            //         {
            //             label: 'AC-1027L',
            //             value: '1027'
            //         },
            //         {
            //             label: 'XN-1033',
            //             value: '1033'
            //         }
            //     ];
        }
    }
</script>


